.card {
    border: 1px solid #f3f3f3;
    padding: 30px;
    margin: 15px;
    width: 220px;
    border-radius: 40px;
    // margin-right: 30px;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;

    &:hover {
        box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.06);
        transform: translateY(-5px);
    }

    .favorite {
        position: absolute;
        cursor: pointer;
    }

    span {
        font-size: 13px;
        opacity: 0.5;
        text-transform: uppercase;
    }

    b {
        font-size: 14px;
    }

    h5 {
        font-weight: 400;
        font-size: 14px;
    }
}

.button {
    background: #ffffff;
    border: 1px solid #f2f2f2;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    height: 32px;
    width: 32px;
    cursor: pointer;
}