.overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    overflow: hidden;
}

.overlayVisible {
    visibility: visible;
    opacity: 1;
    .drawer {
        transform: translateX(0);
    }
}

.drawer {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 420px;
    height: 100%;
    right: 0;
    background: #ffffff;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
    padding: 30px;
    z-index: 100;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    .items {
        flex: 1 1;
        height: 50%;
        overflow: auto;
        margin-bottom: 40px;
    }

    h2 {
        margin: 0;
    }
}

.cartBlock {
    display: flex;
    flex-direction: column;
    height: 90%;
    justify-content: center;
}

.emptyCart {
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    margin-top: 80px;
    strong {
        font-size: 22px;
        font-weight: 600;
        margin-top: 20px;
    }
    p {
        font-size: 16px;
        opacity: 0.7;
    }

    .greenButton {
        width: 245px;
        margin-top: 20px;
        &:hover {
            img {
                transform: translateX(-5px);
            }
        }

        img.emptyArrow {
            transition: transform 0.15s ease-in-out;
            vertical-align: middle;
            padding-right: 15px;
        }
    }
}

.cartTotalBlock {
    display: flex;
    flex-direction: column;
    ul {
        margin-bottom: 40px !important;

        li {
            display: flex;
            align-items: flex-end;
            margin-bottom: 20px;

            div {
                flex: 1;
                height: 1px;
                border-bottom: 1px dashed #dfdfdf;
                position: relative;
                top: -4px;
                margin: 0 7px;
            }
        }
    }

    .greenButton {
        position: relative;

        &:hover {
            img {
                transform: translateX(5px);
            }
        }

        img {
            position: absolute;
            right: 30px;
            top: 20px;
            transition: transform 0.15s ease-in-out;
        }
    }
}

.greenButton {
    width: 100%;
    height: 55px;
    background: #9dd558;
    border-radius: 18px;
    border: 0;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.1s ease-in-out;

    &:hover {
        background: lighten(#9dd558, 5%);
    }

    &:active {
        background: darken(#9dd558, 5%);
    }
}

.cartItem {
    border: 1px solid #f3f3f3;
    border-radius: 20px;
    overflow: hidden;
    padding: 20px;
    margin: 10px 0;
    .cartItemImg {
        height: 70px;
        width: 70px;
        background-size: contain;
        background-position: 0 -3px;
        background-repeat: no-repeat;
        margin-right: 20px;
    }

    p {
        font-size: 16px;
        margin: 0;
    }

    b {
        font-size: 14px;
    }

    .removeBtn {
        opacity: 0.5;
        cursor: pointer;
        transition: opacity 0.15s ease-in-out;

        &:hover {
            opacity: 1;
        }
    }
}